import MyTabHeader from '@/components/MyTabHeader.vue'
export default {
    name: "handle-work-orders",
    components: {
        'my-tab-header': MyTabHeader
    },
    data() {
        return {
            headerList: [
                { name: '待完成', badgeText: 0 },
                { name: '已完成', badgeText: 7 }
            ],
            selectIndex: 0,
            // 待完成列表
            unallocatedList: [
                { number: '13456789', time: '234567' },
                { number: '15555555', time: '234567' },
                { number: '112333', time: '234567' },
                { number: '13456789', time: '234567' },
                { number: '15555555', time: '234567' },
                { number: '112333', time: '234567' }
            ],
            // 已完成列表
            completedList: [
                { number: '323456789', time: '234567' },
                { number: '325555555', time: '234567' },
                { number: '3212333', time: '234567' }
            ],
            // 真实列表
            list:[]
        }
    },
    methods: {
        changeHeader(index) {
            console.log(index, 'index')
            this.selectIndex = index
            if (index === 0) {
                this.list = this.unallocatedList
            }
            if(index === 1) {
                this.list = this.completedList
            }
        },
        gotoHandle() {
            if ( this.selectIndex == 0) {
                this.$router.push({
                    path: '/home/handle',
                    query: {
                        id: '0000'
                    }
                })
            }
            if (this.selectIndex === 1) {
                this.$router.push({
                    path: '/home/completionDetails',
                    query: {
                        id: '0000'
                    }
                })
            }
        }
    },
    mounted() {
        this.list = this.unallocatedList
    }
}
